import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import axios from "axios";
import { uploadPhoto } from "../../actions";
import history from "../../history";
import { Spinner } from "react-bootstrap";

export default function PhotoUpload(props) {
  const [file, setFile] = useState();
  const [uploadedFile, setUploadedFile] = useState();
  const [loading, setLoading] = useState(false);

  let { id } = useParams();

  const dispatch = useDispatch();

  // const test = 3

  const handleChange = (e) => {
    e.preventDefault();
    if (e.target.files[0]) {
      if (e.target.files[0].size > 1097152) {
        alert("Please choose a file smaller than 1MB.");
        window.location.reload();
      }
      setFile(e.target.files[0]);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    const formData = new FormData();
    formData.append("file", file);

    try {
      const res = await axios.post(
        "https://la-recette.herokuapp.com/upload",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );
      const { newFileName, filePath } = res.data;
      setUploadedFile({ newFileName, filePath });
      dispatch(uploadPhoto(res.data.fileName));
      setLoading(false);
    } catch (err) {
      if (err.response.status === 500) {
        console.log(err.response);
        setLoading(false);
      } else {
        console.log(err.response.data.msg);
        setLoading(false);
      }
    }
  };

  const photoLogic = () => {
    if (props.photo) {
      return (
        <div className='existing-image container-fluid'>
          <img
            className='the-image'
            src={`https://storage.googleapis.com/recipe-share-images/${props.photo}`}
            alt='your upload'
          ></img>
          <button
            className='btn btn-dark image-button'
            onClick={() => {
              history.push(`/recipes/editphoto/${id}`);
              window.location.reload();
            }}
          >
            Change Photo?
          </button>
        </div>
      );
    } else if (uploadedFile) {
      return (
        <img
          style={{ width: "50%" }}
          src={uploadedFile.filePath}
          alt='your upload'
        ></img>
      );
    } else {
      return (
        <div>
          <h4>Upload a photo:</h4>

          <form className='photo-form'>
            <div className='input-group mb-3'>
              <div className='input-group-prepend'>
                <input
                  type='file'
                  className='file-input'
                  accept='image/*'
                  label='Choose a file.'
                  id=''
                  onChange={handleChange}
                ></input>
              </div>

              {file && (
                <div className=''>
                  {loading ? (
                    <Spinner animation='border' role='status'>
                      <span className='sr-only'>Loading...</span>
                    </Spinner>
                  ) : (
                    <button
                      type='submit'
                      onClick={handleSubmit}
                      className='btn btn-outline-dark'
                      id=''
                    >
                      Upload
                    </button>
                  )}
                </div>
              )}
              <div></div>
            </div>
          </form>
        </div>
      );
    }
  };

  return (
    <div>
      <div className=''>{photoLogic()}</div>
    </div>
  );
}
