import React from "react";
import PhotoUpload from "./PhotoUpload";
import { Field, reduxForm, FieldArray } from "redux-form";

class RecipeForm extends React.Component {
  // destructuring the formProps which gives us an input object generated by the Field comp and sent back through the reduxForm function

  renderInput({ input, label, meta, type }) {
    return (
      <div>
        <label>{label}</label>
        <input
          {...input}
          autoComplete='off'
          className='form-control'
          type={type}
        />
        <br></br>
        {meta.touched && meta.error && (
          <div className='alert alert-danger'>{meta.error}</div>
        )}
      </div>
    );
  }
  renderFileName = ({ input, label }) => {
    return <div></div>;
  };

  renderMultipleInputs({ input, label, meta }) {
    return (
      <div className='field'>
        <textarea {...input} rows='1' className='form-control'></textarea>
        <br></br>
      </div>
    );
  }

  renderIngredient = ({ fields }, { input, meta, error }) => {
    if (this) {
      return (
        <div className='container render-field'>
          <label>Enter Ingredients</label>
          <ul>
            {fields.map((ingredient, index) => (
              <li key={index}>
                <div className='stack-list'>
                  <div className='stack-item1'>
                    <Field
                      name={ingredient}
                      component={this.renderMultipleInputs}
                      label='Ingredients'
                    ></Field>
                  </div>

                  <div className='stack-item2'>
                    <button
                      className='btn btn-danger'
                      type='button'
                      title='Remove Ingredient'
                      onClick={() => fields.remove(index)}
                    >
                      Remove
                    </button>
                  </div>
                </div>
              </li>
            ))}
            {error && <li className='error'>{error}</li>}
          </ul>
          <button
            className='btn btn-info'
            type='button'
            onClick={() => fields.push()}
          >
            Add New Ingredient
          </button>
        </div>
      );
    } else {
      return <div>Error</div>;
    }
  };

  renderMethod = ({ fields }, { input, label, error }) => {
    if (this) {
      return (
        <div className='container render-field'>
          <label>Enter Method</label>
          <ol>
            {fields.map((method, index) => (
              <li key={index}>
                <div className='stack-list'>
                  <div className='stack-item1'>
                    <Field
                      name={method}
                      component={this.renderMultipleInputs}
                      label='Method'
                    ></Field>
                  </div>
                  <div className='stack-item2'>
                    <button
                      className='btn btn-danger'
                      type='button'
                      title='Remove Step'
                      onClick={() => fields.remove(index)}
                    >
                      Remove
                    </button>
                  </div>
                </div>
              </li>
            ))}
            {error && <li className='error'>{error}</li>}
          </ol>
          <button
            className='btn btn-info'
            type='button'
            onClick={() => fields.push()}
          >
            Add New Method
          </button>
        </div>
      );
    } else {
      return <div>Error</div>;
    }
  };

  onSubmit = (formValues) => {
    this.props.onSubmit({ ...formValues, fileName: this.props.photo });
    console.log({ ...formValues, fileName: this.props.photo });
    console.log(this.props.fileName);
  };

  render() {
    return (
      <div className='container-m create-form-background'>
        <div className='create-form container'>
          <PhotoUpload photo={this.props.fileName} />

          <h4>Enter the recipe:</h4>
          <form
            onSubmit={this.props.handleSubmit(this.onSubmit)}
            className='container-sm'
          >
            {/* handle submit is provided by redux form, we then pass in our own handler function... they call prevent default */}

            <Field
              type='text'
              name='title'
              component={this.renderInput}
              label='Enter Title'
            />
            <Field
              name='description'
              component={this.renderInput}
              label='Enter Description'
            />
            <FieldArray
              name='ingredients'
              component={this.renderIngredient}
              label='Enter Ingredients'
            />
            <FieldArray
              name='methods'
              component={this.renderMethod}
              label='Enter Method'
              validate={this.validate}
            />

            <button className='btn btn-primary btn-block'>Submit</button>
          </form>
        </div>
      </div>
    );
  }
}

const validate = (formValues) => {
  const errors = {};
  if (!formValues.title) {
    errors.title = "Please enter a title.";
  }
  if (!formValues.description) {
    errors.description = "Please enter a description.";
  }
  if (!formValues.ingredients) {
    errors.ingredients = "Please enter at least one ingredient.";
  }
  if (!formValues.methods) {
    errors.methods = { _error: "Please enter at least one step." };
  }
  if (formValues.title && formValues.title.length > 30) {
    errors.title = "Please enter a title of less than 30 characters.";
  }

  return errors;
};
// redux form looks for Field names that match validate errors object names, and passes them into the renderInput function

export default reduxForm({
  form: "recipeForm",
  validate: validate,
})(RecipeForm);
